import { Heading, Stack, Text, SimpleGrid, Link } from '@chakra-ui/layout'
import { useColorMode } from '@chakra-ui/color-mode'
import React from 'react'
import Project from './Project'
import { Button } from '@chakra-ui/button'
import DOMLink from '../generic/DOMLink'
import TTWord from '../home/TTWord'
import { Divider } from "@chakra-ui/react";
import { projects } from '../../data/Projects.tsx';
import { motion } from 'framer-motion';


export default function Projects() {
    let {colorMode, toggleColorMode} = useColorMode();
    const variants = {
        hidden: {opacity: 0, y: 25},
        visible: {opacity: 1, y: 0, transition: {duration: 1, staggerChildren: 0.25}}
    }
    return (
        <Stack w='100%' align='center' direction='column' spacing={16}>
                <Stack direction='column' w={['90%']} align='center'>
                    <Stack
                    direction='column'
                    align='center'
                    w='100%'
                    pt={0}
                    spacing={12}
                    >
                        <motion.div animate="visible" initial={'hidden'} variants={variants}>
                            <Stack textAlign={'center'} spacing={4}>
                                <Heading size={'3xl'}>Projects</Heading>
                                <Text>A photo album of some cool things I've made!</Text>
                                <Divider />
                            </Stack>
                        </motion.div>
                        <motion.div animate={'visible'} initial={'hidden'} variants={variants}>
                            <SimpleGrid columns={[1, null, 2, null, 3]} gap={12}>
                                {projects.map((el, i) => {return (
                                    <motion.div variants={variants} key={i}><Project title={el.title} src={el.imageUrl} tags={el.tags}><Text>{el.body}</Text></Project></motion.div>
                                )})}
                            </SimpleGrid>
                        </motion.div>
                    </Stack>
                </Stack>
        </Stack>
    )
}
