import { Button } from '@chakra-ui/button'
import { useColorMode } from '@chakra-ui/color-mode'
import { Image } from '@chakra-ui/image'
import { Box, Flex, Heading, Stack, Text } from '@chakra-ui/layout'
import { Collapse } from '@chakra-ui/transition'
import { useCycle } from 'framer-motion'
import React, {useEffect, useState} from 'react'
import {Badge, Divider, Tag} from "@chakra-ui/react";
import { TAG_COLORS } from "../../data/TagColors.tsx";

const TAG_COLOR_MAPS = new Map(TAG_COLORS.entries());

export default function Project(props) {
    const [open, toggleOpen] = useCycle(false, true)
    let {colorMode, toggleColorMode} = useColorMode();
    const [tags, setTags] = useState([]);

    useEffect(() => {
        setTags(props.tags.map((el, i) => <Tag m={1} key={i} colorScheme={TAG_COLORS.get(el)}>{el}</Tag>))
    }, []);


    return (
        <Box>
            <Stack
            borderWidth={1}
            borderColor={colorMode === 'light' ? 'gray.100' : 'gray.700'}
            p={4}
            direction='column'
            align='center'
            borderRadius={16}
            shadow={colorMode === 'light' ? 'base' : "14px 14px 28px #171d28, -14px -14px 28px #1d2330;"}
            spacing={12}
            variant='card'
            >
                <Stack spacing={2} w={'100%'}>
                    <Flex align={'center'} justify={'center'} minH={24}>
                        <Heading textAlign='center'>{props.title}</Heading>
                    </Flex>
                    <Flex justify={'center'} wrap={'wrap'}>
                        {tags}
                    </Flex>
                    <Divider />
                </Stack>
                <Flex h={96} w='100%' justify='center' align='center'>
                    {props.src ? <Image src={process.env.PUBLIC_URL + 'project_photos/' + props.src} maxH={96} borderRadius={16}/> : ''}
                </Flex>
                <Box w='100%'>
                    <Collapse in={open} startingHeight={108}>{props.children}</Collapse>
                </Box>
                <Flex flexGrow={1} align={'flex-end'}>
                    <Button onClick={toggleOpen}>{open ? "Show Less" : "Show More"}</Button>
                </Flex>
            </Stack>
        </Box>
    )
}
